<template>
  <div class="item-cell" :style="{height:height + 'px'}">
    <span class="icon">
      <slot name="icon"></slot>
    </span>
    <span class="value">{{value}}</span>
  </div>
</template>

<script>
  /* 小方格组件 */
  export default {
    name: "ItemCell",
    props:{
      value:String,
      height:{
        type:Number,
        default:36
      }
    },
  }
</script>

<style scoped lang="less">
  .item-cell{
    width: 100%;
    padding: 7px 5px;
    display: flex;
    align-items: center;
    justify-content: start;
    cursor: pointer;
    &:hover,&:focus{
      background: @color-lightwhite;
      padding-left: 20px;
      .icon{
        color: @color-skyblue;
      }
      .value{
        color: @color-skyblue;
      }
    }
    .icon{
      font-size: 16px;
    }
    .value{
      padding-left: 10px;
      font-size: 14px;
      font-weight: 600;
      letter-spacing: 1px;
    }
    @media screen{
      @media (max-width: 1200px){
        .icon{
          font-size: 14px;
        }
        .value{
          font-size: 12px;
        }
      }
    }
  }
</style>